.main-container {
  position: relative;


  .home-container {
    position: relative;
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 100vw;
    align-items: center;

    .top {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
      width: 100%;
      background-color: #126258;

      h1 {
        font-family: 'VT323', monospace;
        font-size: 3rem;
        color: #B8E1DD;
      }
    }

    .bottom {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
      width: 100%;
      background-color: #B8E1DD;

      h2 {
        font-family: 'VT323', monospace;
        color: #126258;
      }
    }

    .avatar {
      position: absolute;
      margin-left: auto;
      margin-right: auto;
      margin-top: auto;
      margin-bottom: auto;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      text-align: center;
      border-radius: 100%;
      width: 300px;
      height: 300px;
      object-fit: cover;
      box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
    }
   
  }
}